export default {
	data() {
		return {
			big: 0,
			methods: 2,
		};
	},
	props:{
		count:{
			type: Number
		},
		current:{
			type: Number
		}
	},
	mounted(){
		if(this.methods==1){
			this.point()
		}
	},
	methods:{
		point(){
				let num = 0
			setInterval(()=>{
				this.big=num%3
				num++
			},400)
		},
		prevEv(){
			if(this.current!=1){
				this.$emit('prev',this.current-1)
			}
		},
		nextEv(){
			if(this.current!=this.count){
				this.$emit('next',this.current+1)
			}
		}
	}
}
